// Helper functions
export function getProductById(id: string): Product | undefined {
  return products.find((p) => p.id === id);
}

export function getProductBySku(sku: string): Product | undefined {
  return products.find((p) => p.sku === sku);
}

export function getLocationById(id: string): Location | undefined {
  return locations.find((l) => l.id === id);
}

export function getBoxById(id: string): ShippingBox | undefined {
  return shippingBoxes.find((b) => b.id === id);
}

export function getInventoryForProduct(productId: string): InventoryItem[] {
  return inventory.filter((i) => i.productId === productId);
}

export function getInventoryForLocation(locationId: string): InventoryItem[] {
  return inventory.filter((i) => i.locationId === locationId);
}

export function getTotalQuantity(productId: string): number {
  return inventory
    .filter((i) => i.productId === productId)
    .reduce((sum, i) => sum + i.quantity, 0);
}

export function getBundleAvailability(bundle: Bundle): { available: number; limitingItem: string } {
  let minAvailable = Infinity;
  let limitingItem = '';

  if (!bundle.components) return { available: 0, limitingItem: '' };

  for (const component of bundle.components) {
    const totalQty = getTotalQuantity(component.productId);
    const available = Math.floor(totalQty / component.quantity);
    if (available < minAvailable) {
      minAvailable = available;
      const product = getProductById(component.productId);
      limitingItem = product?.name || component.productId;
    }
  }

  return { available: minAvailable === Infinity ? 0 : minAvailable, limitingItem };
}

export function calculateMargin(price: number, cost: number): number {
  if (price === 0) return 0;
  return ((price - cost) / price) * 100;
}

// Re-export formatting utilities from lib for backwards compatibility
export { formatCurrency, formatCurrencyPrecise, formatNumber, formatPercent } from '@/lib/formatting';

// Get linked label for a product based on its size
export function getLinkedLabel(product: Product): Product | undefined {
  if (!product.linkedLabels || !product.size) return undefined;
  const labelId = product.linkedLabels[product.size];
  if (!labelId) return undefined;
  return getProductById(labelId);
}

// Check label availability for a PO
export function checkLabelAvailability(productId: string, quantity: number): { available: number; shortage: number } {
  const product = getProductById(productId);
  if (!product) return { available: 0, shortage: quantity };

  const label = getLinkedLabel(product);
  if (!label) return { available: quantity, shortage: 0 }; // No label linked

  const labelQty = getTotalQuantity(label.id);
  const shortage = Math.max(0, quantity - labelQty);

  return {
    available: Math.min(labelQty, quantity),
    shortage,
  };
}

// Get low stock items (below reorder point)
export function getLowStockItems(): { product: Product; currentQty: number; reorderPoint: number }[] {
  const lowStock: { product: Product; currentQty: number; reorderPoint: number }[] = [];

  for (const product of products) {
    if (!product.isActive || !product.reorderPoint) continue;
    const totalQty = getTotalQuantity(product.id);
    if (totalQty < product.reorderPoint) {
      lowStock.push({
        product,
        currentQty: totalQty,
        reorderPoint: product.reorderPoint,
      });
    }
  }

  return lowStock.sort((a, b) => (a.currentQty / a.reorderPoint) - (b.currentQty / b.reorderPoint));
}

// Recent orders for dashboard
export function getRecentOrders(limit: number = 5): Order[] {
  return [...orders]
    .sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime())
    .slice(0, limit);
}
